	module.exports = {"data":{"site":{"siteMetadata":{"title":"onAirVR"}},"imageLogo":{"childImageSharp":{"sizes":{"base64":"data:image/png;base64,","aspectRatio":6.317460317460317,"src":"/static/onairvr_logo-0fea2a553704e57f60bfc9a2f4521526-acd71.png","srcSet":"/static/onairvr_logo-0fea2a553704e57f60bfc9a2f4521526-696ce.png 44w,\n/static/onairvr_logo-0fea2a553704e57f60bfc9a2f4521526-ec9e3.png 89w,\n/static/onairvr_logo-0fea2a553704e57f60bfc9a2f4521526-acd71.png 177w,\n/static/onairvr_logo-0fea2a553704e57f60bfc9a2f4521526-eb538.png 266w,\n/static/onairvr_logo-0fea2a553704e57f60bfc9a2f4521526-7a434.png 354w,\n/static/onairvr_logo-0fea2a553704e57f60bfc9a2f4521526-e57cd.png 398w","sizes":"(max-width: 177px) 100vw, 177px"}}},"imageHamburger":{"childImageSharp":{"sizes":{"base64":"data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABQAAAAUCAYAAACNiR0NAAAACXBIWXMAAAsSAAALEgHS3X78AAAARklEQVQ4y2P4T2XAQAsDf1MT08SF26mJGQY/ADqTj5oYZOAHamKaxPIvamKauHADNfHQSIfC1MQgA79RE9Mklv9RE1PdhQB19MeF/3UctQAAAABJRU5ErkJggg==","aspectRatio":1,"src":"/static/icon_hamburger-19e4c12e89a4d7497acebb51cd316393-4fed1.png","srcSet":"/static/icon_hamburger-19e4c12e89a4d7497acebb51cd316393-99b71.png 32w,\n/static/icon_hamburger-19e4c12e89a4d7497acebb51cd316393-63d9a.png 64w,\n/static/icon_hamburger-19e4c12e89a4d7497acebb51cd316393-4fed1.png 128w,\n/static/icon_hamburger-19e4c12e89a4d7497acebb51cd316393-95723.png 192w,\n/static/icon_hamburger-19e4c12e89a4d7497acebb51cd316393-532e6.png 256w","sizes":"(max-width: 128px) 100vw, 128px"}}},"imageBackground":{"childImageSharp":{"sizes":{"base64":"data:image/jpeg;base64,","aspectRatio":3.4329159212880143,"src":"/static/bg_faq-95b3e0880937f7680b828084bc85a083-37b8f.jpg","srcSet":"/static/bg_faq-95b3e0880937f7680b828084bc85a083-fbfd8.jpg 500w,\n/static/bg_faq-95b3e0880937f7680b828084bc85a083-e124f.jpg 1000w,\n/static/bg_faq-95b3e0880937f7680b828084bc85a083-37b8f.jpg 2000w,\n/static/bg_faq-95b3e0880937f7680b828084bc85a083-d8011.jpg 3000w,\n/static/bg_faq-95b3e0880937f7680b828084bc85a083-56734.jpg 3838w","sizes":"(max-width: 2000px) 100vw, 2000px"}}},"contents":{"faq":{"title":"Frequently Asked Questions","description":"\n\n"}},"faqs":{"edges":[{"node":{"internal":{"contentDigest":"14f10440f230514485a65bcd7e810044"},"frontmatter":{"category":"general","question":"What is onAirVR?"},"html":"<p>onAirVR makes a mobile VR device play as a wireless VR headset for a PC by streaming realtime video/audio from the PC to the mobile.</p>"}},{"node":{"internal":{"contentDigest":"fc55f8e04ed302573f2674042727318b"},"frontmatter":{"category":"troubleshooting","question":"My game engine fails to load onAirVR Server plugin."},"html":"<p>It might be due to missing libraries which onAirVR Server depends on. Check out the system reqirements: the latest NVIDIA graphics driver, CUDA Toolkit 8.0.</p>"}},{"node":{"internal":{"contentDigest":"3c5875391511ea7402039f772cfd11cc"},"frontmatter":{"category":"general","question":"Using onAirVR can I play VR games in my PC (for instance, SteamVR games)?"},"html":"<p>No. onAirVR works only with applications built with onAirVR Server. Technically speaking, onAirVR Server – as a game engine plugin – uses dedicated off-screen buffers for rendering and encoding, unlike other similar products which process the screen buffer.</p>"}},{"node":{"internal":{"contentDigest":"85f8c6c149c93632125429e7b37be3f8"},"frontmatter":{"category":"troubleshooting","question":"onAirVR App keeps failing to connect."},"html":"<p>Windows Firewall may block inbound connections to your game engine editor or application you built. Please check Windows Firewall settings and set to allow inbound connections – both TCP and UDP.</p>"}},{"node":{"internal":{"contentDigest":"a16532a622617cb0f658be06f0fe2f15"},"frontmatter":{"category":"troubleshooting","question":"Succeeded to connect but the display of onAirVR App is black or green."},"html":"<p>Update NVIDIA graphics driver of your PC to the latest one. It is worth noting that CUDA Toolkit installation might force to install an older version of NVIDIA graphics driver. If it happens you must update the driver after the installation.</p>"}},{"node":{"internal":{"contentDigest":"d11b21fa1215ff8572d288451e14de9a"},"frontmatter":{"category":"general","question":"Which mobile VR does onAirVR support?"},"html":"<p>Oculus Mobile and Google VR based mobile devices are supported.</p>"}},{"node":{"internal":{"contentDigest":"35e03c6b3ba837756d90f62f495509af"},"frontmatter":{"category":"troubleshooting","question":"I can see my application scenes on onAirVR App but the video frames are often distorted or dropped."},"html":"<p>If your PC runs your application well enough, it is due to the performance of your wireless network in most cases. There are several things you need to check:</p>\n<ul>\n<li>We recommend to <em>use a 5GHz Wi-Fi router</em>. It provides more stable data transmission than 2.4GHz according to our experience in various use cases.</li>\n<li><em>Change the channel of Wi-Fi</em> in your router setting. Do not use channels which your neighbors’ routers are already using.</li>\n<li><em>Hide the Wi-Fi SSID</em> on the router setting. In crowded environments, it helps the router to avoid interference from people’s portable devices searching an available Wi-Fi.</li>\n</ul>"}},{"node":{"internal":{"contentDigest":"f5e24ffc2b32b76d6fadfe052668e6b8"},"frontmatter":{"category":"general","question":"Which graphics card does onAirVR require?"},"html":"<p>onAirVR needs a graphics card capable of hardware-accelerated video encoding. Currently supports NVIDIA graphics cards <sub>(Kepler architecture or later)</sub> only.</p>"}}]}},"pathContext":{}}

